@import url(https://fonts.googleapis.com/css?family=Poppins:400,600);
* {
    margin: 0;
    padding: 0;
}
.bookshelf {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.book {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 30%;
    margin: 1rem .25rem;
}

.book__card {
    position: relative;
    top: 0;
    background-color: #e6feeb;
    min-height: 250px;
    padding: 0.25rem;
    border-radius: 5px;
    box-shadow: #1E4426 5px 5px 15px;
    -webkit-transition: all 0.33s ease-out;
    transition: all 0.33s ease-out;
}

.book__card:hover {
    top: -10px;
    box-shadow: #1E4426 5px 25px 35px;
}

.book__body {
    display: -webkit-flex;
    display: flex;
}
.book__link {
    text-decoration: none;
    color: #333;
}
.book-info__container {
    padding: 0 .75rem;
}

.book-info__text {
    margin: 0.25rem 0;
}

.book__title {
    width: 100%;
    text-align: center;
    padding: .5rem 0;
}

.book__image-container {
    width: 128px;
}

.book__image-thumbnail {
    width: 100%;
    border: 2px solid #1E4426;
}

@media screen and (max-width: 1200px) {
    .book {
        margin: 1rem .10rem;
        width: 32%;
    }
}

@media screen and (max-width: 845px) {
    .book {
        margin: 1rem .10rem;
        width: 48%;
    }
}

@media screen and (max-width: 545px) {
    .book {
        margin: 1.25rem 0;
        width: 90%;
    }
}
* {
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #4C7D57;
}

h3 {
    font-size: 1.25rem;
}

p {
    font-size: .80rem;
}

.container {
    width: 90%;
    max-width: 1400px;
    margin: 0 auto;
}

.project__title {
    text-align: center;
    padding: 1rem;
    color: #E6FEEb;
}

.books__search-form {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 1rem;
}

.books__search-box {
    font-size: 1.5rem;
    color: #1E4426;
    padding: 5px;
    border: none;
}

.btn {
    font-size: 1.5rem;
    background-color: #020903;
    color: #9BC3A4;
    padding: 5px 10px;
    border: none;
}

.btn--submit {
    cursor: pointer;
}

.btn--submit:hover {
    color:#e6feeb;
    background-color:#1e4426;
}

.bookshelf__message {
    padding: 1.5rem 0;
    text-align: center;
}

.loading-message {
    position: absolute;
    bottom: 10px;
    right: 30px;
    opacity: 100%;
    -webkit-animation: fade-in-fade-out 1s infinite;
            animation: fade-in-fade-out 1s infinite;
}

@-webkit-keyframes fade-in-fade-out {
    0% { opacity: 100% }
    45% { opacity: 20% }
    55% { opacity: 20% }
    100% { opacity: 100% }
}

@keyframes fade-in-fade-out {
    0% { opacity: 100% }
    45% { opacity: 20% }
    55% { opacity: 20% }
    100% { opacity: 100% }
}

@media screen and (max-width: 442px) {
    .books__search-box {
        width: 100%;
    }
    .btn {
        width: 100%;
    }
}

@media screen and (max-width: 350px) {
    .container {
        width: 95%;
    }
    .books__search-box {
        font-size: 1.25rem;
    }
    .btn {
        font-size: 1.25rem;
    }
}
